@charset "UTF-8";
/* -------------------
  VARIABLES
--------------------*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap");
@import url("https://use.typekit.net/kab0rei.css");
@import url(https://use.fontawesome.com/releases/v5.3.1/css/all.css);
html {
  font-size: 62.5%;
  color: #333;
  -webkit-font-smoothing: antialiased;
  font-weight: 350;
}

body {
  font-family: Noto Sans CJK JP,  "Noto Sans JP", -apple-system, BlinkMacSystemFont, "Helvetica Neue", YuGothic, " ProN W3", Hiragino Kaku Gothic ProN, Arial, "", Meiryo, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  line-height: 1.6;
  letter-spacing: 0.05em;
}

.main {
  width: 100%;
  margin: 0 auto;
  padding: 50px 0 0;
}

/* -------------------
  RESET
--------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role="list"],
ol[role="list"] {
  list-style: none;
}

html {
  scroll-behavior: auto;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

a {
  text-decoration: none !important;
  outline: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

h1, h2 {
  font-weight: 500;
}

h3, h4 {
  font-weight: 500;
}

h5, h6 {
  font-weight: 450;
}

p {
  font-weight: 400;
}

