/* -------------------
	BREADCRUMB
--------------------*/
.breadcrumb {
  margin: 0;
  padding: 0;
  height: 20px;
  display: flex;
  align-items: center;
}

.breadcrumb-wrapper {
  background-color: white;
}

.breadcrumb-wrapper__content {
  display: flex;
  align-items: center;
  max-width: 1040px;
  margin: auto;
  padding: 0 20px;
  height: 50px;
}

.breadcrumb li {
  font-size: 1.4rem;
  text-align: center;
  position: relative;
  font-weight: 500;
  color: #b9b9b9;
}

.breadcrumb li a {
  color: #333333;
}

.breadcrumb li:not(:last-child) {
  margin-right: 60px;
}

.breadcrumb li:not(:last-child)::after {
  display: inline-block;
  content: '';
  background-image: url("../images/common/icon-arrow-right-black.png");
  font-size: 1.8rem;
  position: absolute;
  width: 7px;
  height: 15px;
  background-size: 100% 100%;
  right: -35px;
  top: 50%;
  transform: translateY(-50%);
}

@media screen and (max-width: 576px) {
  .breadcrumb-wrapper__content {
    padding: 0 15px;
  }
  .breadcrumb li {
    font-size: 1.2rem;
  }
  .breadcrumb li:not(:last-child) {
    margin-right: 40px;
  }
  .breadcrumb li:not(:last-child)::after {
    right: -25px;
    width: 6px;
    height: 15px;
    background-size: 100% 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}

@media screen and (max-width: 389px) {
  .breadcrumb li:not(:last-child) {
    margin-right: 35px;
  }
  .breadcrumb li:not(:last-child)::after {
    right: -22px;
  }
}

/* -------------------
  PAGE TOP
--------------------*/
.page-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 41px;
  height: 41px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  background-color: #C84520;
  transition: all 0.3s;
  z-index: -1;
}

.page-top.active {
