// logo-font
@mixin logo-font() {
    font-family: futura-pt, sans-serif;
    font-weight: $font_medium;
    color: $color_black;
    font-size: 2.0rem;
}
//  base-font
@mixin base-font() {
    font-family: "Noto Sans CJK JP", -apple-system, BlinkMacSystemFont, "Helvetica Neue", YuGothic, " ProN W3",
    Hiragino Kaku Gothic ProN, Arial, "", Meiryo, sans-serif;
    color: $color_black;
}
    